#root {
	background-color: snow;
}
.background {
	/* background-color: grey; */
	margin: auto;
	height: 100%;
}
.homeContainer {
	width: 95vw;
	height: 100%;
	margin: 0 auto;
	/* background-color: #333; */
	color: white;
	display: flex;
}
.homeBanner {
	background-image: url(https://images.unsplash.com/photo-1522252234503-e356532cafd5?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1525&q=80);
	background-color: snow;

	height: 100%;
	opacity: 80%;
	box-shadow: 5px 5px 5px grey;
	margin: 0 auto;
	width: 100%;
	border-radius: 10px;
}
.homeBannerText {
	display: flex;
	flex-direction: column;
	margin: 15rem auto;
}
.homeBannerText h1 {
	text-align: center;
	margin: auto;
	font-size: 6.5rem;
	opacity: 100%;

	text-shadow: 5px 5px 5px #333;

	/* background-color: red; */
	/* box-shadow: 3px 3px 3px #333; */
}
.homeBannerText p {
	text-align: center;
	text-shadow: 4px 4px 4px #333;
	font-size: 2.5rem;
	opacity: 100%;
	color: #ff8484;
	margin-bottom: 1rem;
}
.homeBannerText p:hover {
	opacity: 100%;
	color: white;
}
#pTag2 {
	font-size: 1.3rem;
}
.btn {
	width: 15vw;
	margin: auto auto;
	color: white;
	background-color: #333;
	opacity: 100%;
	font-size: 1.2rem;
	border-radius: 10px;
	padding: 5px;
	box-shadow: 3px 3px 3px snow;
}
.btn:hover {
	color: snow;
	background-color: #ff8484;
}
.aboutMe {
	margin-top: 0;
	margin-bottom: 1rem;
}
.cardHolder {
	border: 3px solid #333;
	height: 40vh;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
}
.card {
	margin: auto auto;
	background-color: #333;
	height: 35vh;
	width: 20vw;
	border: 1px solid white;
}
.btnGetResume {
	margin: 1rem 2rem;
	border-radius: 10px;
	padding: 5px 10px;
	font-size: 1.1rem;
	background-color: #ff8484;
	color: white;
	box-shadow: 2px 2px 2px grey;
	font-weight: bold;
	text-shadow: 2px 2px 2px black;
}
.btnGetResume:hover {
	box-shadow: 3px 3px 3px black;
}

