.aboutMeText {
	box-shadow: 5px 5px 5px grey;
	border-radius: 50% 20% / 10% 15%;
	width: 95%;
	color: black;
	margin: 2rem auto;
	/* border: 1px solid black; */
	padding: 1rem;
	position: relative;
	box-sizing: border-box;
}
.aboutMeText h1 {
	opacity: 100%;
	/* background-color: blue; */
	font-size: 3rem;
	text-decoration: underline;
	margin: 1rem 2rem;
	text-decoration-style: dotted;
	text-decoration-color: white;
}

.aboutMeTexth2 {
	/* background-color: green; */
	margin: 1rem 2rem;
	font-size: 1.2rem;
}
.timothy {
}
.aboutMeText p {
	/* background-color: red; */

	margin: auto 2rem;
	font-size: 1rem;
	line-height: 1.8;
}
.aboutMeText img {
	margin: 1rem 2rem;
}
.webDeveloper {
	color: #ff8484;
}
.webDeveloper:hover {
	color: #333;
}
.aboutMeLinks {
	text-align: center;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
}
.aboutMeLinks a {
	text-decoration: none;
	font-size: 1.3rem;
	color: #ff8484;
	border-radius: 10px;
	border: 1px solid snow;
	padding: 5px 3px;
	background-color: black;
}
.aboutMeLinks a:hover {
	color: snow;
}

#buillding {
}
#box {
}
#office {
}
